.teams {
  width: 100vw;
  min-height: 100vh;
  background-color: #fff;
  padding: 1em 0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.teams h1 {
  font-size: 42px;
  color: var(--primary);
  padding: 1.4em 0;
  width: 100%;
  text-align: center;
}
.teamDiv {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2em;
  transition: 0.4s;
}
.team {
  width: 320px;
  height: 380px;
  padding: 10px;
  margin: 2px 2em;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  overflow: hidden;
  box-shadow: 0 0 4px rgb(148, 147, 147);
  border-radius: 14px;
}

.teamImg {
  width: 80%;
  height: 280px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
}

.teamImg img {
  width: 100%;
  border: 6px solid transparent;
  overflow: hidden;
  filter: drop-shadow(0 0 3px rgb(114, 113, 113));
}

.team h2 {
  font-size: 20px;
  text-align: center;
  color: var(--primary);
}

.team p {
  margin-top: 4px;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  color: var(--primary);
}

