@media (max-width: 1224px) {
  .team {
    width: 300px;
    margin: 2px 10px;
  }
}
@media (max-width: 1062px) {
  .team {
    width: 290px;
    margin: 14px;
  }
}

@media (max-width: 1006px) {
  .team {
    width: 320px;
    margin: 2em;
  }
  .teamDiv {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 796px) {
  .team {
    width: 280px;
    margin: 12px;
  }
}
@media (max-width: 652px) {
  .team {
    width: 240px;
    height: 320px;
    margin: 0;
  }
  .teamImg {
    height: 180px;
  }
}

@media (max-width: 562px) {
  .team {
    width: 200px;
    height: 240px;
    margin: 0;
  }
  .team h2 {
    font-size: 16px;
  }
  .team p {
    font-size: 14px;
  }
}

@media (max-width: 446px) {
  .teams h1 {
    font-size: 26px;
  }
  .team {
    width: 170px;
    height: 220px;
  }
  .teamDiv {
    gap: 1.4em;
  }
}

@media (max-width: 386px) {
  .team {
    width: 150px;
    height: 200px;
  }
  .teamDiv {
    gap: 1em;
  }
  .team h2 {
    font-size: 14px;
  }
  .team p {
    font-size: 12px;
  }
}
