.aboutContainer {
    background-image: url("../../assets/aboutmain.webp");
    /* background-position: center center; */
    background-repeat: no-repeat;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.301);
    width: 100vw;
    height: 100vh;
    padding: 4px 4.2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* opacity: 70%; */
}
.heading {
    width: 50%;
    padding: 12px;
}

.heading h1 {
    color: var(--primary);
    /* color: #fff !important; */
    font-size: 56px;
    font-weight: 600;
}
.moreAbout {
    background-color: var(--primary);
    color: #fff;
    width: 100vw;
    height: 100vh;
    padding: 1em 4em;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}
.aboutCards {
    width: 31%;
    /* height: 432px; */
    height: fit-content;
    margin: 10px;
    /* border: 1px solid #08089e; */
    border-radius: 20px;
    border: 1px solid #fff;
    padding: 1.6rem;
    display: flex;
    flex-direction: column;
}
.logoDiv {
    font-size: 12px;
    margin: 6px 0;
}
.logoDiv i {
    padding: 10px;
    transform: scale(3);
}
.logoDiv h3 {
    display: inline;
    padding: 18px;
}
.aboutCards h2 {
    font-size: 24px;
    margin: 8px auto;
    font-weight: 700;
}
.aboutCards p {
    font-size: 18px;
    line-height: 2rem;
}
.aboutWe {
    width: 100vw;
    height: 100vh;
    background-image: url(../../assets/aboutmain.webp);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 120% 100%;
    padding: 1em 4em;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.manimage {
    width: 48%;
    height: 90%;
}
.manimage img {
    width: 100%;
    height: 100%;
    border-radius: 0 250px 250px 250px;
}
.aboutDetail {
    width: 50%;
    padding: 1rem;
}
.aboutDetail h1 {
    font-size: 36px;
    padding: 10px;
    color: var(--primary) !important;
}
.aboutDetail p {
    font-size: 18px;
    line-height: 1.85em;
    color: var(--primary) !important;
}
.misVis {
    width: 100vw;
    min-height: 100vh;
    max-height: fit-content;
    background-color: var(--primary);
    color: #fff;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 8px 4.2em;
}
.inMisVis {
    width: 35%;
    height: 448px;
    margin: 10px;
    padding: 1.8em;
    border-radius: 20px;
    border: 1px solid #fff;
}
.inMisVis h1 {
    font-size: 36px;
    padding: 10px;
    font-weight: 700;
}
.inMisVis p {
    font-size: 18px;
    letter-spacing: 1px;
    line-height: 1.5em;
}
.inMisVis a {
    color: inherit;
    text-decoration: underline;
}
@media (max-width: 1100px) {
    .inMisVis {
        width: 35%;
        height: fit-content;
    }
}
@media (max-width: 1052px) {
    .moreAbout {
        height: fit-content;
    }
}

@media (max-width: 1002px) {
    .aboutCards {
        width: 44%;
    }
    .aboutWe {
        height: fit-content;
    }
    .manimage {
        width: 90%;
        height: auto;
    }
    .aboutDetail {
        width: 94%;
        padding: 12px;
    }
    .inMisVis {
        width: 94%;
        height: fit-content;
    }
}
@media (max-width: 796px) {
    .aboutContainer {
        height: 600px;
        background-position: 100%;
    }
    .aboutCards {
        width: 98%;
        align-items: center;
        justify-content: space-evenly;
    }
}
@media (max-width: 796px) {
    .heading h1 {
        font-size: 46px;
    }
}
@media (max-width: 652px) {
    .aboutContainer {
        align-items: center;
        justify-content: center;
        padding: 2em;
    }
    .heading {
        width: 100%;
    }
    .creativeDiv {
        padding: 2em;
    }
    .aboutWe {
        padding: 1em;
    }
}
@media (max-width: 562px) {
    .heading h1 {
        font-size: 38px;
    }
    .moreAbout {
        padding: 1em;
    }
    .aboutDetail {
        width: 98%;
    }
    .misVis {
        padding: 1em;
    }
    .inMisVis p {
        font-size: 16px;
        letter-spacing: 0px;
    }
}
@media (max-width: 398px) {
    .moreAbout h2 {
        font-size: 20px;
    }
    .moreAbout p {
        font-size: 16px;
    }
}
