.technologies {
  width: 100vw;
  height: fit-content;
  min-height: 100vh;
  background-color: #ffffff;
  padding: 10px 4em;
  flex-wrap: wrap;
  box-shadow: 0 0 4px rgb(148, 147, 147);
}

.technologies h1 {
  color: var(--primary);
  text-align: center;
  padding: 1em;
}

.bar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 1.5em;
}

.bar ul {
  width: 85%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.bar ul li {
  /* color: var(--primary); */
  color: #000;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  padding: 4px 8px;
  width: fit-content;
  transition: 0.5s ease-in-out;
  border-bottom: 4px solid transparent;
}

.front,
.back,
.mobile,
.db,
.fullstack,
.cloud,
.testingtools {
  display: none;
  transition: 0.4s;
}

li.active {
  border-bottom: 4px solid var(--primary) !important;
  transition: 0.5s ease-in-out;
}

.open {
  width: 94%;
  margin: 1em auto;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 20px;
  justify-content: space-evenly;
  justify-items: center;
  align-content: space-evenly;
  align-items: center;
  transition: 0.4s;
}

.tech {
  width: 160px;
  height: 180px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  cursor: pointer;
}

.techimage {
  width: 120px;
  height: 120px;
  border-radius: 6px;
  border: 1px solid var(--primary);
  box-shadow: 0 0 2px rgb(148, 147, 147);
  display: flex;
  align-items: center;
  justify-content: center;
}
.techimage img {
  width: 80px;
}

.tech h2 {
  color: #000;
  text-align: center;
  font-size: 18px;
}

@media (max-width: 998px) {
  .bar ul {
    width: 98%;
  }
  .bar ul li {
    font-size: 16px;
  }
  .open {
    width: 100%;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 796px) {
  li.active {
    /* background-color: grey !important;
    padding: 8px 12px !important;
    color: #fff !important;
    border-radius: 20px;
    font-weight: 700;
    border: transparent !important; */
    font-size: 14px !important;
  }
  .technologies{
    padding: 10px 1.6em;
  }
  .bar{
    width: 100%;
    overflow: scroll;
    justify-content: flex-start;
  }
  .bar::-webkit-scrollbar{
    display: none;
}
  .bar ul{
    width: fit-content;
  }
  .bar ul li{
    width: 126px;
  }
  .bar ul::-webkit-scrollbar {
    width: 0; 
    display: none;
  }
  .open {
    grid-template-columns: repeat(3, 1fr);
  }
  .tech{
    width: 180px;
  }
  .techimage{
    width: 140px;
    height: 140px;
  }
  .techimage img {
    width: 72px;
  }
}
@media (max-width: 592px) {
  .open {
    width: 100%;
    grid-template-columns: repeat(2, 1fr);
  }
  .bar ul li{
    font-size: 14px;
    width: max-content;
  }
  .tech{
    width:140px;
  }
  .techimage{
    width: 120px;
    height: 120px;
  }
  .techimage img{
    width: 66px;
    height: 66px;
  }
  .technologies h1 {
    font-size: 20px ;
    padding: 1em 0;
  }
}
