.contactAlert {
    background: white;
    padding: 1em 10px;
    border-radius: 6px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    width: 480px;
    height: 440px;
}

.contactAlert h2 {
    font-size: 24px;
    text-align: center;
    padding: 12px;
    color: #333;
}
.contactAlert form {
    padding: 12px 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 1.8em;
}

.contactAlert input {
    width: 92%;
    padding: 14px 20px;
    border-radius: 10px;
    background-color: #c5c3c3af;
    border: 1px solid #c5c3c3;
    box-shadow: 0 0 4px #a8a7a7af;
    font-size: 16px;
}
.contactAlert textarea {
    width: 92%;
    height: 140px;
    padding: 14px 20px;
    border-radius: 10px;
    background-color: #c5c3c3af;
    border: 1px solid #c5c3c3;
    box-shadow: 0 0 4px #a8a7a7af;
    font-size: 16px;
}
.contactAlert button {
    text-align: center;
    font-size: 16px;
    padding: 12px 20px;
    background-color: var(--primary);
    color: #fff;
    border-radius: 30px;
    border: transparent;
    letter-spacing: 1px;
    font-weight: 700;
}
