footer {
  width: 100vw;
  background-image: url(../../assets/footer1.jpg);
  /* background-color: rgb(246, 247, 252); */
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 1em 3.2em 1em 4.2em;
  height: fit-content;
}

footer::before {
  position: absolute;
  /* content: ""; */
  width: calc(100vw + 4px);
  height: 430px;
  /* top: 0; */
  left: 0;
  background: linear-gradient(118deg, black, black, rgb(5, 2, 34), #75c1f0);
  opacity: 60%;
  margin: -15px -4px;
}

.foot {
  height: 80%;
  /* height: max-content; */
  display: flex;
  align-items: center;
  /* justify-content: space-evenly; */
  position: relative;
  color: var(--textColor);
  padding-top: 1em;
  /* flex-wrap: wrap; */
}

.inFoot {
  width: 39%;
  height: 100%;
  padding: 10px 0;
  margin: 1em 12px;
  display: flex;
  flex-direction: column;
}

.inFoot img {
  width: 230px;
  padding: 0 0 8px 0;
}

.inFoot p {
  width: 90%;
  font-size: 18px;
  padding: 12px 0;
  line-height: 2em;
  color: #fff;
}
.inFoot h1 {
  font-size: 20px;
  color: #fff;
}
.inFoot ul {
  width: 100%;
  height: 100%;
  display: flex;
  margin-top: 12px;
  flex-direction: column;
}
.inFoot li {
  margin: 1rem 0;
}
.infoot ul li a i {
  color: var(--primary);
}

.inFoot ul li a {
  display: flex;
  align-items: center;
  font-size: 18px;
  color: #fff;
  gap: 10px;
}

.footIcon {
  width: 100%;
}

.footDetail {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 2em 12px;
}

.footDetail p {
  color: var(--textColor);
  width: 70%;
}

.linkIcon {
  width: 30%;
}

.linkIcon ul {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.linkIcon ul li a {
  padding: 6px;
  color: #ffffff;
}

.linkIcon ul li:hover {
  color: var(--primary);
}

@media (max-width: 1206px) {
  .inFoot img {
    width: 180px;
  }
  .inFoot ul li a,
  .inFoot p {
    font-size: 16px;
  }
  footer {
    padding: 10px 3.8em;
  }
}
@media (max-width: 1006px) {
  footer {
    padding: 10px 4.2em;
  }
  .foot {
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .inFoot {
    width: 48%;
    padding: 10px 0;
    margin: 0;
  }
  .inFoot p {
    width: 100%;
    padding: 8px 0;
  }
  .inFoot:nth-child(1) {
    width: 100%;
  }
  .inFoot:nth-child(2) {
    justify-content: start;
  }
  .footDetail {
    padding: 2em 0;
  }
}
@media (max-width: 796px) {
  footer {
    padding: 10px 1.6em;
  }
  .footDetail {
    padding: 8px;
    flex-wrap: wrap;
    flex-direction: column-reverse;
  }
  .footDetail p {
    text-align: center;
    width: 100%;
    margin: 10px auto 0 auto;
  }

  .linkIcon {
    width: 80%;
    margin: 10px auto;
  }
}
@media (max-width: 652px) {
  .inFoot {
    width: 100%;
  }
  .inFoot img {
    width: 160px;
  }
  .inFoot ul li {
    margin: 10px 0;
  }
  .infoot ul li a {
    font-size: 16px;
  }
}

@media (max-width: 398px) {
  .linkIcon {
    width: 85%;
  }
}
