.servicesSec {
    /* background-color: var(--primary); */
    background-color: #fff;
    position: relative;
    width: 100vw;
    min-height: 100vh;
    height: -moz-fit-content;
    height: -webkit-fit-content;
    height: fit-content;
    padding: 2em 4em;
    box-shadow: 0 0 4px rgb(148, 147, 147);

  }
  .servicesSec h1{
    width: 100%;
    padding: 8px 0 1em 12px;
    text-align: center;
    /* color: #fff; */
    color: var(--primary);
    font-size: 42px;
  }
  
  .servicesDiv {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 1.2em;
  }
    
  .services {
    background-color: #fff;
    max-height: fit-content;
    width: 340px;
    height: 310px;
    padding: 12px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: space-between; */
    box-shadow: 0 0 4px rgb(148, 147, 147);
    gap: 1rem;
  }

  .services:hover{
    box-shadow: 0 0 4px var(--primary);

  }
  
  .servicesImg {
    width: 52px;
    height: 52px;
    margin: 6px auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .servicesImg img{
    width: 100%;
  }  
  
  .services h3 {
    
    font-size: 18px;
    color: var(--primary);
    /* padding: 8px auto; */
    margin: 0 0 6px 0;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }
  
  .services p {
    color: rgb(56, 55, 55);
    font-size: 16px;
    text-align: center;
    margin: 0 0 10px 0;
    word-spacing: normal !important;
    line-height: 25px;

  }



  @media(max-width:796px) {
    .servicesDiv {
      justify-content: center;
      gap: 1em;
    }
    .services {
        width: 80%;
        margin: auto;
      }
      }


  @media(max-width:562px){

    .servicesSec {
    padding: 1em 10px;

    }
    .servicesSec h1 {
        font-size: 30px;
      }
      .services {
        width: 95%;
        margin: auto;
      }
  }
  @media(max-width:398px){
    .servicesDiv {
        /* margin-top: -2.6em; */
        padding: .6em;
      }
      .servicesSec h1 {
        font-size: 28px;
      }
    
      .services {
        /* margin: 18px auto; */
        width: 96%;
        gap: 2px;
        height: 300px;
      }
      .servicesImg{
        width: 50px;
        height: 50px;
      }
      .services h3{
        font-size: 14px;
        margin: 0;
      }
      .services p{
        font-size: 14px;
        margin: 0;
      }

  }