.main {
  position: absolute;
  margin-top: 80px;
}

.mainBody {
  position: fixed;
  width: 100vw;
  height: 100%;
  background-image: url(https://images.unsplash.com/photo-1504384308090-c894fdcc538d?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1470&q=80);
  /* background-color: rgb(246, 247, 252); */
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}

.mainBody::before {
  position: absolute;
  content: "";
  width: 100vw;
  height: 100vh;
  /* background:linear-gradient(118deg,#314477,#3cb1e2,#87d1ff); */
  background: linear-gradient(118deg, black, black, rgb(5, 2, 34), #75c1f0);
  opacity: 60%;
}

.mainContainer {
  width: 100vw;
  /* height: 695px; */
  height: 100vh;
  background-image: url(https://images.unsplash.com/photo-1504384308090-c894fdcc538d?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1470&q=80);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 1px;
}

.mainContainer::before {
  position: absolute;
  content: "";
  width: 100vw;
  height: 100vh;
  background: linear-gradient(118deg, black, black, rgb(5, 2, 34), #75c1f0);
  opacity: 60%;
  top: 0;
  right: 0;
}

.Container {
  margin-top: 75px;
  display: flex;
  align-items: center;
  width: 100%;
}

.Headings {
  position: relative;
  width: 48%;
  height: 80vh;
  margin: 3rem 2px 2px 5rem;
  display: flex;
  /* align-items: center; */
  justify-content: space-evenly;
  flex-direction: column;
  z-index: 20;
}

.Headings h1 {
  font-size: 28px;
  font-weight: 600;
  color: var(--textColor);
  /* text-shadow:0 0 7px #fff; */
}

.Headings h2 {
  font-size: 80px;
  font-weight: 1000;
  color: var(--textColor);
  /* text-shadow:3px 2px 6px #fff; */
}

.Headings p {
  font-size: 18px;
  letter-spacing: 0.7px;
  line-height: 1.8rem;
  padding: 10px 28px 10px 0;
  line-height: 2.4rem;
  color: var(--textColor);
  /* text-shadow:0 0 3px #fff; */
}

.btns {
  padding: 15px 0;
  width: 322px;
  display: flex;
  justify-content: space-between;
}

.portfolioBtn {
  text-align: center;
  font-size: 16px;
  padding: 15px;
  background-color: var(--primary);
  color: #fff;
  border-radius: 30px;
  border: transparent;
  letter-spacing: 1px;
  transition: 0.8s;
  font-weight: 700;
}

.contactBtn {
  letter-spacing: 1px;
  background-color: transparent;
  font-size: 16px;
  color: var(--primary);
  border: 2px solid var(--primary);
  border-radius: 30px;
  padding: 12px 16px;
  transition: 0.8s;
  font-weight: 700;
  z-index: 20;
}

.contactBtn:hover {
  background-color: var(--primary);
  color: #fff;
}

.imagee {
  width: 50%;
}

.imagee img {
  width: 65%;
}

.designs {
  width: 100vw;
  min-height: 100vh;
  height: fit-content;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  /* margin: 2em auto; */
  padding: 10px 4.2em;
  background-color: #fff !important;
}

.aboutDesigns {
  width: 51%;
  padding: 10px;
}

.designs h3 {
  font-size: 36px;
  font-weight: 500;
  color: var(--primary);
}

.designs p {
  font-size: 20px;
  letter-spacing: 1px;
  line-height: 1.3em;
  font-weight: 400;
  padding: 14px 10px;
}

.groupImg {
  width: 48%;
  background-color: #fff !important;
}

.groupImg img {
  width: 100%;
  border-radius: 20px;
}

@media (max-width: 1200px) {
  .Headings {
    width: 60%;
  }
  .aboutDesigns {
    padding: 10px 12px 0 0;
    line-height: auto;
    width: 46%;
  }
  .designs h3 {
    font-size: 26px;
  }
  .designs p {
    font-size: 18px;
    padding: 14px 0;
  }
  .groupImg {
    width: 54%;
  }
}
@media (max-width: 1006px) {
  .Headings {
    width: 80%;
  }

  .Headings h1 {
    font-size: 28px;
  }

  .Headings h2 {
    font-size: 68px;
  }
  .Headings p {
    font-size: 16px;
  }
  .designs h3 {
    font-size: 20px;
    letter-spacing: auto;
  }
  .designs p {
    font-size: 16px;
    letter-spacing: auto;
  }
}
@media (max-width: 926px) {
  .Container {
    justify-content: center;
  }

  .Headings {
    width: 80%;
    margin: 1.8em auto;
  }
  .aboutDesigns {
    margin-top: 1em;
    width: 90%;
  }
  .designs h3 {
    font-size: 36px;
  }
  .groupImg {
    width: 80%;
  }
}
@media (max-width: 796px) {
  .Headings {
    width: 90%;
  }

  .Headings h1 {
    font-size: 20px;
  }

  .Headings h2 {
    font-size: 62px;
  }

  .btns {
    width: 328px;
  }
  .designs {
    padding: 10px 2.2em;
  }
  .aboutDesigns {
    width: 100%;
  }
  .groupImg {
    width: 100%;
  }
}
@media (max-width: 652px) {
  .Container {
    justify-content: center;
    height: calc(100% - 75px);
  }

  .Headings {
    width: 90%;
    height: 100%;
    margin: 2em 0 0 0;
    display: flex;
    justify-content: space-evenly;
  }

  .Headings h1 {
    font-size: 20px;
  }

  .Headings h2 {
    font-size: 56px;
  }
  .designs h3 {
    font-size: 26px;
  }
}
@media (max-width: 562px) {
  .mainContainer {
    height: 100vh;
  }
  .btns {
    width: 100%;
    flex-flow: column;
    align-items: center;
  }

  .btns button {
  }
  .portfolioBtn {
    margin-bottom: 1em;
  }
  .Headings {
    justify-content: center;
    gap: 12px;
  }
}
@media (max-width: 468px) {
  .Container {
    width: 100%;
  }
  .Headings h2 {
    font-size: 46px;
  }
  .Headings p {
    padding: 0;
    width: 100%;
  }
  .logoa {
    width: 190px;
  }
  .log img {
    width: 180px;
  }
  .portfolioBtn,
  .contactBtn {
    font-size: 14px;
  }
}

@media (max-width: 398px) {
  .Headings {
    margin-top: 1.2em;
    width: 94%;
  }

  .btns {
    padding: 4px;
  }
  .logoa {
    width: 150px;
  }
  .log img {
    width: 150px;
  }
}
