.singleCard {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(177, 170, 170, 0.438);
    z-index: 100;
}
.closeButton {
    color: var(--primary);
    position: absolute;
    top: 50px;
    right: 50px;
    padding: 8px 16px;
    border-radius: 10px;
    background-color: #fff;
    cursor: pointer;
}
.member {
    width: 420px;
    height: 540px;
    padding: 10px;
    background: #fff;
    border-radius: 10px;
}
@keyframes image {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
.memberImg {
    width: 220px;
    margin: auto;
    height: 260px;
    overflow: hidden;
}
.memberImg img {
    width: 100%;
    animation: image 0.5s ease-in;
}
.member h2 {
    font-size: 20px;
    color: var(--primary);
    padding: 8px 0;
    text-align: center;
}
.member p {
    font-size: 18px;
    color: var(--primary);
    padding: 8px 0;
    text-align: center;
}
.discription {
    margin: auto;
    width: 90%;
    font-size: 16px;
    padding: 4px 0;
    line-height: 26px;
}
.icons {
    width: 100px;
    margin: 4px auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.icons li a {
    color: var(--primary);
}

@media (max-width: 1006px) {
    .closeButton {
        top: 10px;
        right: 10px;
        padding: 8px 16px;
    }
}
@media (max-width: 426px) {
    .member {
        height: 562px;
    }
}

